<template>
  <div class="anquan">
    <van-nav-bar
      title="安全中心"
      left-text="返回"
      left-arrow
      @click-left="back"
    />
    <div class="zz"></div>
    <div class="xg">
      <van-cell @click="isp" title="修改密码" />
      <van-cell @click="destroyAccount" title="注销账号" />
    </div>

    <van-popup
      v-model="isopen"
      closeable
      close-icon="close"
      round
      position="bottom"
    >
      <div class="zchz">
        <div class="zc">修改密码</div>
        <div class="form-box">
          <van-field
            left-icon="newspaper-o"
            @click-right-icon="toggle"
            v-model="userRegisterInfo.passwords"
            :type="istype ? 'text' : 'password'"
            label="新密码"
            placeholder="输入密码(6~16位)"
            :right-icon="istype ? 'eye-o' : 'closed-eye'"
          />
          <van-field
            left-icon="newspaper-o"
            @click-right-icon="toggle"
            v-model="userRegisterInfo.password"
            :type="istype ? 'text' : 'password'"
            label="旧密码"
            placeholder="输入密码(6~16位)"
            :right-icon="istype ? 'eye-o' : 'closed-eye'"
          />

          <div class="btn-box">
            <van-button
              round
              @click="updatePassword"
              block
              color="linear-gradient(to bottom, skyblue 0%, pink 100%)"
              >确&nbsp;&nbsp;&nbsp;定</van-button
            >
          </div>
        </div>
      </div>
    </van-popup>

    <div class="btn-box">
      <van-button
        round
        block
        @click="logout"
        color="linear-gradient(to bottom, skyblue 0%, pink 100%)"
        >退出登录</van-button
      >
    </div>
  </div>
</template>

<script>
import { usjl } from "../assets/js/usjl.js";
export default {
  name: "Anquan",
  data() {
    return {
      userRegisterInfo: {
        password: "",
        passwords: "",
      },

      isopen: false,
      istype: false,
    };
  },

  created() {
    // this.updatePassword()
    // this.destroyAccount()
    // this.retrievePassword()
    // this.emailValidCode()
  },

  methods: {
    back() {
      this.$router.go(-1);
    },

    isp() {
      this.isopen = true;
    },


    toggle() {
      this.istype = !this.istype;
    },

    //修改密码
    updatePassword() {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        
        return this.$router.push({ name: "Login" });
      }

      let yz = {
        passwords: {
          value: this.userRegisterInfo.passwords,
          reg: /^[A-Za-z]\w{5,15}$/,
          error: "密码支持字母数字_组合首字符为字母",
        },
        password: {
          value: this.userRegisterInfo.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          error: "密码支持字母数字_组合首字符为字母",
        },
      };

      if (!usjl.validForm(yz)) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: this.baseUrl + "/updatePassword",

        data: {
          appkey: this.appkey,
          tokenString,
          password: this.userRegisterInfo.passwords,
          oldPassword: this.userRegisterInfo.password,
        },
      })
        .then((res) => {
          
          this.$toast.clear();
          // 
          if (res.data.code == "E001") {
              this.isopen=false;
              for(let key in this.userRegisterInfo){
                this.userRegisterInfo[key] = '';
              }
          } else {
            this.$toast({
              message: res.data.msg,
              forbidClick: true,
              duration: 3000,
            });
          }
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //退出登录
    logout() {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        
        return this.$router.push({ name: "Login" });
      }

      //   this.$toast.loading({
      //     message: "加载中...",
      //     forbidClick: true,
      //     duration: 0,
      //   });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/logout",
        data: {
          appkey: this.appkey,
          tokenString,
        },
      })
      this.$dialog.confirm({
          title:"退出账号",
          message:'是否确定退出账号'
      })

        .then(() => {
          this.$toast({
              message:"退出登录成功",
              forbidClick:true,
              duration:700
          });
          setTimeout(()=>{

              this.$router.push({name:"Login"});
              this.$toast.clear();
          },700)
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },

    //注销账户
     destroyAccount() {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        
        return this.$router.push({ name: "Login" });
      }

      //   this.$toast.loading({
      //     message: "加载中...",
      //     forbidClick: true,
      //     duration: 0,
      //   });

      this.axios({
        //请求方式
        method: "POST",
        url: this.baseUrl + "/destroyAccount",
        data: {
          appkey: this.appkey,
          tokenString,
        },
      })
      this.$dialog.confirm({
          title:"注销账号",
          message:'是否确定注销账号'
      })

        .then(() => {
          this.$toast({
              message:"注销成功",
              forbidClick:true,
              duration:700
          });
          setTimeout(()=>{
              
              this.$router.push({name:"Login"});
              this.$toast.clear();
          },700)
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },


    // emailValidCode() {
    //   let tokenString = localStorage.getItem("tokenString");

    //   if (!tokenString) {
    //     
    //     return this.$router.push({ name: "Login" });
    //   }

    //   // let yz = {
    //   //   passwords: {
    //   //     value: this.userRegisterInfo.passwords,
    //   //     reg: /^[A-Za-z]\w{5,15}$/,
    //   //     error: "密码支持字母数字_组合首字符为字母",
    //   //   },
    //   //   password: {
    //   //     value: this.userRegisterInfo.password,
    //   //     reg: /^[A-Za-z]\w{5,15}$/,
    //   //     error: "密码支持字母数字_组合首字符为字母",
    //   //   },
    //   // };

    //   // if (!usjl.validForm(yz)) {
    //   //   return;
    //   // }

    //   this.$toast.loading({
    //     message: "加载中...",
    //     forbidClick: true,
    //     duration: 0,
    //   });

    //   this.axios({
    //     method: "POST",
    //     url: this.baseUrl + "/emailValidCode",

    //     data: {
    //       appkey: this.appkey,
    //       email:this.email
    //     },

    //   })
    //     .then((res) => {
    //       
    //       this.$toast.clear();
    //       // 
    //       if (res.data.code == "E001") {
    //           this.isopen=false;
    //           for(let key in this.userRegisterInfo){
    //             this.userRegisterInfo[key] = '';
    //           }
    //       } else {
    //         this.$toast({
    //           message: res.data.msg,
    //           forbidClick: true,
    //           duration: 3000,
    //         });
    //       }
    //     })
    //     .catch((err) => {
    //       this.$toast.clear();
    //       
    //     });
    // },

  },
};
</script>

<style lang="less" scoped>
.anquan {
  .zz {
    width: 100%;
    height: 120px;
    background: chocolate;
  }
  .xg {
    width: 95%;
    position: relative;
    top: -20px;
    left: 8px;
  }
  .form-box {
    margin-top: 50px;
  }

  .btn-box {
    margin-top: 50px;
  }

  .forgot {
    text-align: right;
    margin-top: 10px;
    color: #999;
  }

  .register-btn {
    margin-top: 30px;
  }
  .zchz {
    padding: 15px;
  }
  .zc {
    color: #666;
    font-size: 18px;
  }
}
</style>